  import React from 'react';
  import { useState } from 'react';
  import { Button, Card, ListGroup, Form,  } from 'react-bootstrap';

  import { text } from '../convenience/Text.js';
  import {toQ,toA,toH} from '../convenience/Function.js'


export default function Challenges() {

  const [index,setIndex] = useState(Math.floor( Math.random() * text.length ));

  return (
    <div>

      <br></br>
      <Card style={{ width: '36rem' }}>
        <ListGroup variant="flush">
          <ListGroup.Item>問題：{toQ(text[index])}</ListGroup.Item>
        </ListGroup>
      </Card>

      <br></br>

      <Form>
        <Form.Group controlId="formBasicEmail">
          <Form.Label>Enter Your Answer !</Form.Label>
          <Form.Control placeholder="ここに入力" />
          {/* <Form.Text className="text-muted">
            We'll never share your email with anyone else.
          </Form.Text> */}
        </Form.Group>

        <Button variant="primary" type="submit">Submit</Button>{' '}
        <Button variant="primary" onClick={()=>setIndex(Math.floor( Math.random() * text.length ))}>Next</Button>
      </Form>

    </div>
  );
}