import React from 'react';
import { useState } from 'react';
import { Button, Card, ListGroup } from 'react-bootstrap';

import { text } from '../convenience/Text.js';
import {toQ,toA,toH} from '../convenience/Function.js'

export default function Problems() {

  const [index,setIndex] = useState(Math.floor( Math.random() * text.length ));
  const [isAns,setIsAns] = useState(false);
  const [isHint,setIsHint] = useState(false);
  
  const setNext = () => {
    setIsAns(false);
    setIsHint(false);
    setIndex(Math.floor( Math.random() * text.length ));
  }

  return (
    <div>
      
      <br></br>

      <Card style={{ width: '36rem' }}>
        <ListGroup variant="flush">
          <ListGroup.Item>問題：{isHint?toH(text[index]):toQ(text[index])}</ListGroup.Item>
          {isAns ? <ListGroup.Item>答え：{toA(text[index])}</ListGroup.Item>:<ListGroup.Item>答え：</ListGroup.Item>}
        </ListGroup>
      </Card>

      <br></br>
    
      <Button variant="primary" onClick={()=>setIsHint(IsHint => !IsHint)}>Ask Hint</Button>{' '}
      <Button variant="primary" onClick={()=>setIsAns(IsAns => !IsAns)}>Show Answer</Button>{' '}
      <Button variant="primary" onClick={setNext}>Next</Button>
      
    </div>
  );
}