export function toQ(x){
  let memo = x.split('');
  memo.sort();
  let str = '';
  for(let i=0;i<memo.length;i++) str+=memo[i];
  return str;
}

export function toA(x){
  return x;
}

export function toH(x){
  let memo = x.split('');
  let first = memo[0];
  memo[0] = ' '; // } がzよりも遅い
  memo.sort();
  let str = first;
  for(let i=1;i<memo.length;i++) str+=memo[i];
  return str;
}